
.vue-tabs .tabs {
  background: #f5f5f5;
  height: 48px;
  border: 0;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 1000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.vue-tabs .tabs .tab {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #8d8888;
  text-align: center;
  margin-top: 6px;
  font-size: 12px;
}
.vue-tabs .tabs .tab.active {
  color: #ff3e3e;
}
.vue-tabs .tabs .tab .tab-icon {
  font-size: 20px;
  margin-bottom: 2px;
}


