
.vue-gift .mask {
  background: #000;
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1600;
  opacity: 0.5;
  width: 100%;
  height: 100%;
}
.vue-gift .mask-enter-active,
.vue-gift .mask-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  opacity: 0.5;
}
.vue-gift .mask-enter,
.vue-gift .mask-leave-active {
  opacity: 0;
}
.vue-gift .tab {
  height: 40px;
  width: 100%;
  background: rgba(255,255,255,0.95);
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 2000;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-top: 1px solid #ff7c7c;
  box-shadow: 0 0 6px rgba(0,0,0,0.4);
}
.vue-gift .tab .tab-receive {
  float: left;
  padding: 8px 16px;
  height: 24px;
  font-size: 14px;
  color: #333;
}
.vue-gift .tab .tab-receive .icon {
  line-height: 24px;
  color: #ff605b;
  font-size: 16px;
  margin-right: 4px;
}
.vue-gift .tab .tab-present {
  float: right;
  display: block;
  width: 58px;
  height: 38px;
  background-color: #ff605b;
  margin: -4px 16px 0 0;
  text-align: center;
  border-radius: 4px;
}
.vue-gift .tab .tab-present .icon {
  color: #fff;
  line-height: 36px;
  font-size: 22px;
}
.vue-gift .present {
  position: fixed;
  left: 0;
  width: 100%;
  bottom: 0;
  z-index: 1900;
  background: #fff;
}
.vue-gift .present .present-title {
  display: inline-block;
  height: 40px;
  line-height: 40px;
  padding: 0 8px;
  white-space: nowrap;
}
.vue-gift .present .present-title .title-item {
  display: inline-block;
  margin: 0px 8px;
}
.vue-gift .present .present-content {
  background: #8a2be2;
  padding: 10px 0;
  height: 255px;
}
.vue-gift .present .present-content .test1 {
  height: 100%;
  background: #ccc;
}
.vue-gift .present .present-content .test2 {
  height: 100%;
  background: #ff0;
}
.vue-gift .present .present-content .test3 {
  height: 100%;
  background: #999;
}
.vue-gift .present .present-footer {
  background: #fff;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 2px 16px;
  position: relative;
  height: 36px;
}
.vue-gift .present-enter-active,
.vue-gift .present-leave-active {
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.vue-gift .present-enter,
.vue-gift .present-leave-active {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}


